body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*
Color Scheme:

Wet Brick: #bf564c
Pale Brick: #ff7366
Skin: #ffbfb2
Lavender: #5c2d91
Heavy Sky: #6897e8
 */
@font-face {
  font-family: 'SpicyRice';
  src: url(/static/media/SpicyRice-Regular.25d5b567.otf) format('opentype');
}
@font-face {
  font-family: 'Antipasto';
  src: url(/static/media/antipastopro-demibold-webfont.d499e521.woff) format('woff');
}
.top-level-container {
  position: absolute;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

/* LOGIN AND SIGN UP SCREEN STYLING */

body {
  font-family: 'Antipasto';
}

button {
  color: #5c2d91;
}

.start-screen-container {
  position: relative;
  background-color: #6897e8;
  height: 1000px;
  margin-top: -50px;
  width: 100%;
}

.start-screen-container h1 {
  font-family: 'SpicyRice';
  font-size: 4em;
  text-align: center;
  color: #5C2D91;
  padding-top: 15%;
}

.start-screen-container #logo-right h1 {
  padding: 0;
  margin: -90px;
  color: white;
  font-weight: 300;

}

.animate-logo-left {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-animation: 0.4s ease-out 0.7s 1 slideinfromleft;
          animation: 0.4s ease-out 0.7s 1 slideinfromleft;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.animate-logo-right {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  -webkit-animation: 0.4s ease-out 1.4s 1 slideinfromright;
          animation: 0.4s ease-out 1.4s 1 slideinfromright;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes slideinfromleft {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@keyframes slideinfromleft {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@-webkit-keyframes slideinfromright {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@keyframes slideinfromright {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

.start-screen-container h3 {
  margin-top: 100px;
  margin-bottom: 0;
  color: white;
  font-weight: 900;
  font-size: 2em;
  text-align: center;
}

.start-screen-container input {
  outline: none;
  display: block;
  color: white;
  font-size: 1.2em;
  width: 75%;
  height: 50px;
  margin-left: auto;
  margin-right: auto;
  border: none;
  border-bottom: white solid 1px;
  background-image:none;
  background-color:transparent;
  box-shadow: none;
}

::-webkit-input-placeholder {
   font-family: 'Antipasto';
   color: white;
   font-weight: 50;
   opacity: 0.4;
}

::-ms-input-placeholder {
   font-family: 'Antipasto';
   color: white;
   font-weight: 50;
   opacity: 0.4;
}

::placeholder {
   font-family: 'Antipasto';
   color: white;
   font-weight: 50;
   opacity: 0.4;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active  { /* mega hack, omg wat */
  -webkit-transition: "color 9999s ease-out, background-color 9999s ease-out";
  -webkit-transition-delay: 9999s;
}

.start-screen-container a {
  text-decoration: none;
}

.go-back-container {
  color: white;
  text-align: center;
  margin-top: 20px;
}

.start-screen-container button {
  display: block;
  font-size: 14px;
  font-weight: 500;
  border-radius: 50px;
  margin-left: auto;
  margin-right: auto;
  background-color: white;
  -webkit-transition: font-size 0.1s ease-out 0s;
  transition: font-size 0.1s ease-out 0s;
}


.center-vertically {
  position: absolute;
  width: 100%;
  top: 40%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}



/* User Navbar Styling */

.user-navbar-container ul {
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
  background-color: #5c2d91;
  height: 20px;
  color: white;
  font-size: 19px;
  font-weight: 900;
  margin: 0;
}

.user-navbar-container ul > li {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.user-navbar-container button {
  background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

